
import {request} from '../network/request'
export const getToken={
    components:{},
    data(){
        return {}
    },
    methods:{
        getbytoken(data){
            return request({
                url: '/members/getbytoken',
                method: 'post',
                data
            })
        
        }
    },
    computed:{}
}


