<template>
    <div id="mepage" ref="me">
        <BlogCard></BlogCard>
        <EditorMd></EditorMd>
        <AppList :personal="applist"></AppList>
        <MuiscPlayer v-if="this.$store.state.musiclist.length != 0" ></MuiscPlayer>
        <FileLoad ref="file" @filescroll="filescroll"></FileLoad>
        <DataChartsVue></DataChartsVue>
    </div>
</template>
<script>
import FileLoad from 'components/common/fileload';

import BlogCard from 'components/content/blogcard';
import AppList from 'components/content/applist';
import MuiscPlayer from 'components/content/musicplayer';
import EditorMd from 'components/content/editormd';

import DataChartsVue from './childpages/DataCharts.vue';


import { getToken } from '@/maxins'



export default {
    name: 'mepage',
    data() {
        return {
            applist: [
                {
                    img: require('assets/img/appimg/add.png'),
                    info: ''
                },
                {
                    img: require('assets/img/appimg/up.png'),
                    info: ''
                }
            ],
            show: false,
            bloglist: []
        }
    },
    mixins: [getToken],
    created() {

        this.$store.commit('updatemusic')
        console.log(this.$store.state.musiclist)
    },
    mounted() {
        //console.log(this.$store)
        
        if (this.$store.state.token != '') {
            var data = {
                token: this.$store.state.token
            }
            this.getbytoken(data).then(res => {
                this.$store.commit('commituser', res.data)
            })
        } else {
            this.$toast.show('', 3000)
            this.$router.push('/login')
        }
    },
    components: {
        BlogCard,
        AppList,
        MuiscPlayer,
        FileLoad,
        DataChartsVue,
        EditorMd
    },
    methods: {
        filescroll() {
            //dom$el
            this.$nextTick(() => {
                document.documentElement.scrollTo(0, this.$refs.file.$el.offsetTop)
            })
        },

    }
}
</script>
<style scoped>
#mepage {
    background-color: rgba(100, 100, 100, .4);
}
</style>


