<template>
    <div class="cardlist" v-if="isscroll">
        <div class="cardleft" v-if="index % 2 == 0">
            <img :src="card.imgUrl" alt="">
        </div>
        <div class="cardright">
            <div class="title">
                <img :src="this.$store.state.currentuser.headimg" alt="">
                {{ card.title }}</div>
            <p>{{ card.content }}</p>
            <span>{{ card.sign }}</span>
        </div>
        <div class="cardleft" v-if="index % 2 !== 0">
            <img :src="card.imgUrl" alt="">
        </div>
    </div>
</template>
<script>
import { reveal01, revealleft, revealright } from 'assets/js/scrollreveal.js'
export default {
    name: 'cardlist',
    props: {
        card: {
            type: Object,
            default() {
                return {
                    title: '',
                    content: '~',
                    sign: '~',
                    imgUrl: 'https://cdn.jsdelivr.net/gh/Tony-hurry/imageBad@main//study-images/linux01.png'
                }
            }
        },
        index: {
            type: Number,
            default() {
                return 0
            }
        }

    },
    data() {
        return {
            number: this.index,
            isscroll: true
        }
    },
    created() {

        this.$nextTick(() => {
            if (this.index % 2 == 0) {
                revealleft('.cardlist', this.index)
            } else {
                revealright('.cardlist', this.index)
            }
        })
    },
    mounted() {
        //console.log(this.$store.state.currentuser)
    },
    methods: {

    }
}
</script>
<style scoped lang="less">
//992px 
@media screen and (min-width : 992px) {
    .cardlist {
        width: 100%;
        height: 400px;

        display: flex;
        padding: 5px;
        border-bottom: 2px solid white;
        font-size: 50px;
        .cardleft {
            margin-right: 5px;
            flex: 2;

            img {
                border-radius: 5px;
                width: 100%;
                height: 100%;
            }
        }

        .cardright {
            flex: 3;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            background-color: #fff;
            border-radius: 5px; 
            overflow: hidden;
            .title {
                color:#fff;
                background-color: rgba(100, 100, 100, .4);
                
                height:100px;
                font-size: 70px;
                line-height: 100px;
                border-radius: 5px 5px 0 0;
                
                img{
                    align-items: center;
                    float: left;
                    border-radius: 50%;
                    transform: scale(0.7);
                    width:100px;
                    height:100px; 
                }
            }

            p {
                flex: 5;
                color: black;
                font-size: 30px;
                padding:2px;
                text-indent: 1em;
                border-bottom: 1px solid orange;
            }

            span {
                flex: 1;
                height:40px;
                font-size: 20px;
                padding-right: 10px;
                text-align: right;
                line-height: 40px;
                color: green;
            }
        }
    }
}


// 768px 991px 
@media screen and (min-width:768px) and (max-width:991px) {
    .cardlist {
        width: 100%;
        height: 400px;

        display: flex;
        padding: 5px;
        border-bottom: 2px solid white;
        font-size: 50px;
        .cardleft {
            margin-right: 5px;
            flex: 2;

            img {
                border-radius: 5px;
                width: 100%;
                height: 100%;
            }
        }

        .cardright {
            flex: 3;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            background-color: #fff;
            border-radius: 5px; 
            overflow: hidden;
            .title {
                color:#fff;
                background-color: rgba(100, 100, 100, .4);
                
                height:80px;
                font-size: 70px;
                line-height: 80px;
                border-radius: 5px 5px 0 0;
                
                img{
                    align-items: center;
                    float: left;
                    border-radius: 50%;
                    transform: scale(0.7);
                    width:80px;
                    height:80px; 
                }
            }

            p {
                flex: 5;
                color: black;
                font-size: 30px;
                padding:2px;
                text-indent: 1em;
                border-bottom: 1px solid orange;
            }

            span {
                flex: 1;
                height:40px;
                font-size: 20px;
                padding-right: 10px;
                text-align: right;
                line-height: 40px;
                color: green;
            }
        }
    }
}


//767px 
@media screen and (max-width:767px) {
    .cardlist {
        width: 100%;
        height: 400px;

        display: flex;
        flex-direction: column;
        padding: 5px;
        border-bottom: 2px solid white;
        font-size: 50px;
        .cardleft {
            margin-right: 5px;
            flex: 2;

            img {
                border-radius: 5px;
                width: 100%;
                height: 100%;
            }
        }

        .cardright {
            flex: 3;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            background-color: #fff;
            border-radius: 5px; 
            overflow: hidden;
            .title {
                color:#fff;
                background-color: rgba(100, 100, 100, .4);
                
                height:70px;
                font-size: 70px;
                line-height: 70px;
                border-radius: 5px 5px 0 0;
                
                img{
                    align-items: center;
                    float: left;
                    border-radius: 50%;
                    transform: scale(0.7);
                    width:70px;
                    height:70px; 
                }
            }

            p {
                flex: 5;
                color: black;
                font-size: 30px;
                padding:2px;
                text-indent: 1em;
                border-bottom: 1px solid orange;
            }

            span {
                flex: 1;
                height:40px;
                font-size: 20px;
                padding-right: 10px;
                text-align: right;
                line-height: 40px;
                color: green;
            }
        }
    }
}
</style>


